// React
import React, { useState, useEffect } from 'react';

// React router
import { Link } from 'react-router-dom';

// M-U Components
import {
  AppBar,
  Toolbar,
  Tabs,
  Tab,
  Button,
  Menu,
  MenuItem,
} from '@material-ui/core';
import useScrollTrigger from '@material-ui/core/useScrollTrigger';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/styles';

function ElevationScroll(props) {
  const { children } = props;

  const trigger = useScrollTrigger({
    disableHysteresis: true,
    threshold: 0,
  });

  return React.cloneElement(children, {
    elevation: trigger ? 4 : 0,
  });
}

// useSytyles hooks
const useStyles = makeStyles((theme) => ({
  toolbarMagin: {
    ...theme.mixins.toolbar,
  },
  tabContainer: {
    marginLeft: 'auto',
  },
  tab: {
    ...theme.typography.tab,
    minWidth: 10,
    marginLeft: '25px',
  },
  logoContainer: {
    padding: 0,
    '&:hover': {
      backgroundColor: 'transparent',
    },
  },
  menuItem: {
    ...theme.typography.tab,
    fontSize: 14,
    opacity: 0.7,
    '&:hover': {
      opacity: 1,
    },
  },
}));

const Header = (props) => {
  const classes = useStyles();

  // Use states Hooks
  const [value, setValue] = useState(0);
  const [anchorEl, setAnchorEl] = useState(null);
  const [open, setOpen] = useState(false);
  const [selectedIndex, setSelectedIndex] = useState(0);

  // Handler Functions
  const handleChange = (e, value) => {
    setValue(value);
  };

  const handleClick = (e) => {
    setAnchorEl(e.currentTarget);
    setOpen(true);
  };

  const handleMenuItemClick = (e, i) => {
    setAnchorEl(null);
    setOpen(false);
    setSelectedIndex(i);
  };

  const handleClose = (e) => {
    setAnchorEl(null);
    setOpen(false);
  };

  const menuOptions = [
    { name: 'Para propriet谩rios', link: '/para-proprietarios' },
    { name: 'Anunciar im贸vel para alugar', link: '/anunciar-para-alugar' },
    { name: 'Anunciar im贸vel para vender', link: '/anunciar-para-vender' },
    { name: 'Meus Im贸veis', link: '/meus-imoveis' },
  ];

  useEffect(() => {
    if (window.location.pathname === '/' && value !== 0) {
      setValue(0);
    } else if (
      window.location.pathname === '/imoveis-para-alugar' &&
      value !== 1
    ) {
      setValue(1);
    } else if (
      window.location.pathname === '/imoveis-para-comprar' &&
      value !== 2
    ) {
      setValue(2);
    } else if (
      window.location.pathname === '/para-proprietarios' &&
      value !== 3
    ) {
      setValue(3);
    } else if (window.location.pathname === '/quem-somos' && value !== 4) {
      setValue(4);
    } else if (window.location.pathname === '/contato' && value !== 5) {
      setValue(5);
    }

    switch (window.location.pathname) {
      case '/':
        if (value !== 0) {
          setValue(0);
        }
        break;
      case '/imoveis-para-alugar':
        if (value !== 1) {
          setValue(1);
        }
        break;
      case '/imoveis-para-comprar':
        if (value !== 2) {
          setValue(2);
        }
        break;
      case '/para-proprietarios':
        if (value !== 3) {
          setValue(3);
          setSelectedIndex(0);
        }
        break;
      case '/anunciar-para-alugar':
        if (value !== 3) {
          setValue(3);
          setSelectedIndex(1);
        }
        break;
      case '/anunciar-para-vender':
        if (value !== 3) {
          setValue(3);
          setSelectedIndex(2);
        }
        break;
      case '/meus-imoveis':
        if (value !== 3) {
          setValue(3);
          setSelectedIndex(3);
        }
        break;
      case '/quem-somos':
        if (value !== 4) {
          setValue(4);
        }
        break;
      case '/contato':
        if (value !== 5) {
          setValue(5);
        }
        break;
      default:
        break;
    }
  }, [value]);

  return (
    <>
      <ElevationScroll>
        <AppBar position='fixed'>
          <Toolbar>
            <Button
              component={Link}
              to='/'
              className={classes.logoContainer}
              onClick={() => setValue(0)}
              disableRipple
            >
              <Typography variant='h5'>
                Alugue na HORA <i className='far fa-clock'></i>
              </Typography>
            </Button>
            <Tabs
              value={value}
              onChange={handleChange}
              className={classes.tabContainer}
              indicatorColor='primary'
            >
              <Tab
                className={classes.tab}
                component={Link}
                to='/'
                label='Home'
              />
              <Tab
                className={classes.tab}
                component={Link}
                to='/imoveis-para-alugar'
                label='Im贸veis para alugar'
              />
              <Tab
                className={classes.tab}
                component={Link}
                to='/imoveis-para-comprar'
                label='Im贸veis para comprar'
              />
              <Tab
                aria-owns={anchorEl ? 'simple-menu' : undefined}
                aria-haspopup={anchorEl ? 'true' : undefined}
                className={classes.tab}
                component={Link}
                onMouseOver={(event) => handleClick(event)}
                to='/para-proprietarios'
                label='Para propriet谩rios'
              />
              <Tab
                className={classes.tab}
                component={Link}
                to='/quem-somos'
                label='Quem somos'
              />
              <Tab
                className={classes.tab}
                component={Link}
                to='/contato'
                label='Contate nos'
              />
            </Tabs>
            <Menu
              id='simple-menu'
              anchorEl={anchorEl}
              open={open}
              onClose={handleClose}
              classes={{ paper: classes.menu }}
              MenuListProps={{ onMouseLeave: handleClose }}
            >
              {menuOptions.map((option, i) => (
                <MenuItem
                  key={option}
                  component={Link}
                  to={option.link}
                  classes={{ root: classes.menuItem }}
                  onClick={(event) => {
                    handleMenuItemClick(event, i);
                    setValue(1);
                    handleClose(true);
                  }}
                  selected={i === selectedIndex && value === 1}
                >
                  {option.name}
                </MenuItem>
              ))}
            </Menu>
          </Toolbar>
        </AppBar>
      </ElevationScroll>
      <div className={classes.toolbarMagin} />
    </>
  );
};
export default Header;
